import { getSms, forgetPassword } from '@/api'
import inputCell from '@/components/input-cell.vue';
import clickButton from '@/components/click-button'
export default {
	data() {
		return {
			phone: '',
			sms: '',
			pwd: '',
			pwd2: '',
			count: 0
		};
	},
	methods: {
		async getCode () {  // 获取验证码
			let phoneReg = /^1[3456789]\d{9}$/g;
			if(this.phone == "") {
				uni.showToast({
					title: '请输入您的手机号',
					icon: 'none'
				})
				return;
			}
			let result = await getSms({
				mobile: this.phone,
				event: 'reset_pwd'
			})
			if(result.recode == 20023) {
				uni.showToast({
					title: '验证码发送成功',
				})
				this.count = 60;
				let timer = setInterval(() => {
					this.count --;
					if(this.count <= 0) {
						clearInterval(timer)
					}
				}, 1000)
			}else {
				uni.showToast({
					title: result.remsg,
					icon: 'none'
				})
			}
		},
		async buttonClick () { // 确定修改
			let result = await forgetPassword({
				mobile: this.phone,
				captcha: this.sms,
				password1: this.pwd,
				password2: this.pwd2
			})
			console.log(result)
			if(result.recode == 10000) {
				uni.showToast({
					title: '修改成功',
				})
				setTimeout(() => {
					uni.redirectTo({
						url: '/pages/login/login/login'
					})
				}, 2000)
			}else {
				uni.showToast({
					title: result.remsg,
					icon: 'none'
				})
			}
		}
	},
	components: {
		inputCell,
		clickButton
	},
};
